import koaRoute from "koa-router"; // koa 
import {router as networkRouter} from "../bizMod/network"; //networkRouter   
import {router as downloadRouter} from "../bizMod/download"; //downloadRouter   

class router {
  constructor(app, parentRouter) {
    this.app = app;
    this.router = parentRouter;
    this.init();
  }
  createRouter() {
    this.twoLevelRoute = new koaRoute({
      prefix: "/performance", // 
    });
    return this.twoLevelRoute;
  }
  middleware() {
    // 404
    // this.app.use('/user',function* (next) {
    //     try {
    //         yield* next;
    //     } catch (e) {
    //         this.status = 500;
    //         this.body = '500';
    //     }
    //     if (parseInt(this.status) === 404) {
    //         this.body = '404';
    //     }
    // });
  }
  // 
  addRouters() {
    // script
    new networkRouter(this.app,this.twoLevelRoute);
    new downloadRouter(this.app,this.twoLevelRoute);

   
    // 
    this.router.use(this.twoLevelRoute.routes()); //
    
  }
  init() {
    // 
    this.createRouter();
    // 
    this.middleware();
    // 
    this.addRouters();
  }
}

export default router;