import controller from "../controller";
import koaRoute from "koa-router"; // koa 

class router {
  constructor(app, parentRouter) {
    this.app = app;
    this.router = parentRouter;
    this.init();
  }
  createRouter() {
    this.threeLevelRoute = new koaRoute({
      prefix: "/user", // 
    });
    return this.threeLevelRoute;
  }
  middleware() {
    // 404
    // this.app.use('/user',function* (next) {
    //     try {
    //         yield* next;
    //     } catch (e) {
    //         this.status = 500;
    //         this.body = '500';
    //     }
    //     if (parseInt(this.status) === 404) {
    //         this.body = '404';
    //     }
    // });
  }
  // 
  addRouters() {
    // 
    this.query();
    this.add();
    this.edit();
    this.router.use(this.threeLevelRoute.routes()); //
  }
  init() {
    // 
    this.createRouter();
    // 
    this.middleware();
    // 
    this.addRouters();
  }
  query() {
   
    //  
    this.threeLevelRoute.get("/query", controller.query);
  }
  add() {
    //  
    this.threeLevelRoute.post("/add", controller.add);
  }
  edit() {
    //  
     this.threeLevelRoute.post("/edit", controller.edit);
  }
}

export default router;
