import "@babel/polyfill";
import path from "path";
import koa from "koa";
import koaStatic from "koa-static";
import { renderToString } from "react-dom/server";
import { promise } from "./utils";
import { CheckTable, connection, exec, addUser } from "./db/index.js";
import { Redis } from "./redis";
import Route from "./routes/index";
import kill from "kill-port";
import { port } from "./config";

class App {
  constructor() {
    this.app = new koa();
    this.init();
    var obj = {
      name: "1",
      age: 2,
    };
  }
  async init() {
    // redis
    // await this.connectRedis();
    // 
    // await this.connectSql();
    //
    this.addRoute();
    // 
    this.listen();
  }
  async addStatic() {
    koaStatic(path.join(__dirname, "../build"), {
      maxage: 365 * 24 * 60 * 1000,
      index: "root",
      // 'index'localhost:3030index.html
    });
  }
  async connectRedis() {
    await promise((reslove, reject) => {
      Redis.connect(() => {
        console.log("Redis ");
        reslove();
      });
      Redis.error(() => {
        console.log("Redis ");
        reject();
      });
    });
  }
  async connectSql() {
    await promise((reslove, reject) => {
      connection.connect((err) => {
        if (err) {
          console.log("");
          reject();
          throw err;
        }
        new CheckTable();
        console.log("mysql");
        reslove();
      });
    });
  }
  addRoute() {
    // 
    new Route(this.app);
  }
  listen() {
    // try {
    //    kill(port, "tcp");
    // } catch (e) {}

    this.server = this.app.listen(port, () => {
      console.log(`:http://localhost:${port}/`);
    });

    this.server.setTimeout(5 * 60 * 1000);
  }
}

export default new App();
