import mysql from 'mysql';
import { MYSQL_CONF } from '../config/index';
 

// 
const connection  = mysql.createConnection(MYSQL_CONF)


//  sql 
const  exec = async (sql)=> {
    return   await  new Promise((resolve, reject) => {
        console.log('sql=',sql)
        connection.query(sql, (err, result) => {
            if (err) {
                reject(err)
                return
            }
            resolve(result)
        })
    })
}

export {
    connection,
    exec,
}
